// Product data with consistent image naming
const products = [
    { id: 1, name: "Wireless Headphones", price: 59.99 },
    { id: 2, name: "Smart Watch", price: 129.99 },
    { id: 3, name: "Bluetooth Speaker", price: 39.99 },
    { id: 4, name: "Laptop Backpack", price: 49.99 },
    { id: 5, name: "Fitness Tracker", price: 79.99 },
    { id: 6, name: "Wireless Earbuds", price: 89.99 }
].map(product => ({
    ...product,
    image: `https://via.placeholder.com/300?text=${encodeURIComponent(product.name)}`
}));

// Cart functionality
let cart = JSON.parse(localStorage.getItem('cart')) || [];

// Update cart count
function updateCartCount() {
    const count = cart.reduce((total, item) => total + item.quantity, 0);
    document.querySelectorAll('.cart-count').forEach(el => {
        el.textContent = count;
    });
}

// Add to cart
function addToCart(productId, productName, productPrice) {
    const existingItem = cart.find(item => item.id === productId);
    
    if (existingItem) {
        existingItem.quantity += 1;
    } else {
        cart.push({
            id: productId,
            name: productName,
            price: productPrice,
            quantity: 1
        });
    }
    
    localStorage.setItem('cart', JSON.stringify(cart));
    updateCartCount();
    if (window.location.pathname.includes('cart.html')) {
        renderCart();
    }
}

// Remove from cart
function removeFromCart(productId) {
    cart = cart.filter(item => item.id !== productId);
    localStorage.setItem('cart', JSON.stringify(cart));
    updateCartCount();
    renderCart();
}

// Update quantity
function updateQuantity(productId, newQuantity) {
    const item = cart.find(item => item.id === productId);
    if (item) {
        item.quantity = parseInt(newQuantity) || 1;
        localStorage.setItem('cart', JSON.stringify(cart));
        renderCart();
    }
}

// Render cart
function renderCart() {
    const cartItemsEl = document.getElementById('cart-items');
    const subtotalEl = document.getElementById('subtotal');
    const totalEl = document.getElementById('total');
    const checkoutBtn = document.querySelector('.checkout-btn');
    
    if (cart.length === 0) {
        cartItemsEl.innerHTML = `
            <div class="empty-cart">
                <p>Your cart is empty</p>
                <a href="products.html" class="btn">Continue Shopping</a>
            </div>
        `;
        subtotalEl.textContent = '$0.00';
        totalEl.textContent = '$0.00';
        checkoutBtn.disabled = true;
        return;
    }
    
    const html = cart.map(item => {
        const product = products.find(p => p.id === item.id);
        const itemTotal = item.price * item.quantity;
        
        return `
            <div class="cart-item">
                <img src="${product.image}" alt="${item.name}">
                <div class="cart-item-details">
                    <h4>${item.name}</h4>
                    <p class="cart-item-price">$${item.price.toFixed(2)}</p>
                    <div class="cart-item-quantity">
                        <button class="quantity-btn minus" data-id="${item.id}">-</button>
                        <input type="number" value="${item.quantity}" min="1" data-id="${item.id}">
                        <button class="quantity-btn plus" data-id="${item.id}">+</button>
                    </div>
                </div>
                <button class="remove-item" data-id="${item.id}">
                    <i class="fas fa-trash"></i>
                </button>
            </div>
        `;
    }).join('');
    
    const subtotal = cart.reduce((sum, item) => sum + (item.price * item.quantity), 0);
    
    cartItemsEl.innerHTML = html;
    subtotalEl.textContent = `$${subtotal.toFixed(2)}`;
    totalEl.textContent = `$${subtotal.toFixed(2)}`;
    checkoutBtn.disabled = false;
    
    // Add event listeners
    document.querySelectorAll('.remove-item').forEach(btn => {
        btn.addEventListener('click', (e) => {
            removeFromCart(parseInt(e.target.closest('button').dataset.id));
        });
    });
    
    document.querySelectorAll('.quantity-btn.minus').forEach(btn => {
        btn.addEventListener('click', (e) => {
            const id = parseInt(e.target.dataset.id);
            const item = cart.find(item => item.id === id);
            if (item.quantity > 1) {
                updateQuantity(id, item.quantity - 1);
            }
        });
    });
    
    document.querySelectorAll('.quantity-btn.plus').forEach(btn => {
        btn.addEventListener('click', (e) => {
            const id = parseInt(e.target.dataset.id);
            const item = cart.find(item => item.id === id);
            updateQuantity(id, item.quantity + 1);
        });
    });
    
    document.querySelectorAll('.cart-item-quantity input').forEach(input => {
        input.addEventListener('change', (e) => {
            const id = parseInt(e.target.dataset.id);
            updateQuantity(id, e.target.value);
        });
    });
}

// Render products
function renderProducts(productsToRender) {
    const container = document.getElementById('products-container');
    if (!container) return;
    
    const html = productsToRender.map(product => `
        <div class="product-card">
            <img src="${product.image}" alt="${product.name}">
            <h3>${product.name}</h3>
            <p class="price">$${product.price.toFixed(2)}</p>
            <button class="btn add-to-cart" data-id="${product.id}" data-name="${product.name}" data-price="${product.price}">Add to Cart</button>
        </div>
    `).join('');
    
    container.innerHTML = html;
    
    // Add event listeners to add to cart buttons
    document.querySelectorAll('.add-to-cart').forEach(btn => {
        btn.addEventListener('click', (e) => {
            const id = parseInt(e.target.dataset.id);
            const name = e.target.dataset.name;
            const price = parseFloat(e.target.dataset.price);
            addToCart(id, name, price);
            
            // Show added to cart feedback
            const originalText = e.target.textContent;
            e.target.textContent = 'Added!';
            e.target.style.backgroundColor = '#2E7D32';
            setTimeout(() => {
                e.target.textContent = originalText;
                e.target.style.backgroundColor = '#4CAF50';
            }, 1000);
        });
    });
}

// Search products
function searchProducts() {
    const searchInput = document.getElementById('search-input');
    const sortSelect = document.getElementById('sort-select');
    
    if (!searchInput || !sortSelect) return;
    
    searchInput.addEventListener('input', filterAndSortProducts);
    sortSelect.addEventListener('change', filterAndSortProducts);
    
    function filterAndSortProducts() {
        const searchTerm = searchInput.value.toLowerCase();
        let filteredProducts = products.filter(product => 
            product.name.toLowerCase().includes(searchTerm)
        );
        
        // Sort products
        switch(sortSelect.value) {
            case 'price-low':
                filteredProducts.sort((a, b) => a.price - b.price);
                break;
            case 'price-high':
                filteredProducts.sort((a, b) => b.price - a.price);
                break;
            default:
                // Default sorting (by ID)
                filteredProducts.sort((a, b) => a.id - b.id);
        }
        
        renderProducts(filteredProducts);
    }
}

// Initialize the page
document.addEventListener('DOMContentLoaded', function() {
    updateCartCount();
    
    if (window.location.pathname.includes('cart.html')) {
        renderCart();
    } else if (window.location.pathname.includes('products.html')) {
        renderProducts(products);
        searchProducts();
    } else {
        // Home page - add event listeners to add to cart buttons
        document.querySelectorAll('.add-to-cart').forEach(btn => {
            btn.addEventListener('click', (e) => {
                const id = parseInt(e.target.dataset.id);
                const name = e.target.dataset.name;
                const price = parseFloat(e.target.dataset.price);
                addToCart(id, name, price);
                
                // Show added to cart feedback
                const originalText = e.target.textContent;
                e.target.textContent = 'Added!';
                e.target.style.backgroundColor = '#2E7D32';
                setTimeout(() => {
                    e.target.textContent = originalText;
                    e.target.style.backgroundColor = '#4CAF50';
                }, 1000);
            });
        });
    }
});